import React, { Suspense } from 'react'; // Suspense   ,   
import { Routes, Route } from 'react-router-dom';
import Navbar from './components/common/Navbar';
import Footer from './components/common/Footer';
import ProtectedRoute from './components/layout/ProtectedRoute'; //    
import { FullPageSpinner } from './components/common/LoadingSpinner'; //  Suspense fallback

//         ()
const HomePage = React.lazy(() => import('./pages/HomePage'));
const LoginPage = React.lazy(() => import('./pages/LoginPage'));
const RegisterPage = React.lazy(() => import('./pages/RegisterPage'));
const ProfilePage = React.lazy(() => import('./pages/ProfilePage'));
const ChatPage = React.lazy(() => import('./pages/ChatPage'));
// CreateChatPage,   ,     HomePage,
//     ,     .
// const CreateChatPage = React.lazy(() => import('./pages/CreateChatPage'));
const NotFoundPage = React.lazy(() => import('./pages/NotFoundPage'));

function App() {
  return (
    //  Bootstrap      
    <div className="d-flex flex-column min-vh-100 app-container">
      <Navbar />
      {/*    */}
      {/* Suspense ,   React.lazy   */}
      <Suspense fallback={<FullPageSpinner message="Loading page..." />}>
        <main className="container mt-4 flex-grow-1"> {/* mt-4     */}
          <Routes>
            {/*   */}
            <Route path="/" element={<HomePage />} />
            <Route path="/login" element={<LoginPage />} />
            <Route path="/register" element={<RegisterPage />} />

            {/*   (   ) */}
            {/* ProtectedRoute  <Outlet />     */}
            <Route element={<ProtectedRoute />}>
              <Route path="/profile" element={<ProfilePage />} />
              <Route path="/chat" element={<ChatPage />} />
              {/*  CreateChatPage   :
              <Route path="/create-chat" element={<CreateChatPage />} />
              */}
            </Route>

            {/*     (404) */}
            <Route path="*" element={<NotFoundPage />} />
          </Routes>
        </main>
      </Suspense>
      <Footer />
    </div>
  );
}

export default App;