import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../../hooks/useAuth'; //  
import { Navbar as BootstrapNavbar, Nav, NavDropdown, Image, Container } from 'react-bootstrap'; //  Bootstrap
import defaultAvatar from '../../assets/images/default-avatar.png'; //   
import { getFullImageUrl } from '../../utils/helpers'; //    URL 

const Navbar = () => {
  const { currentUser, logout } = useAuth(); //      
  const navigate = useNavigate();

  const handleLogout = () => {
    logout(); //     AuthContext
    navigate('/login'); //    
  };

  return (
    <BootstrapNavbar bg="dark" variant="dark" expand="lg" sticky="top" className="shadow-sm">
      <Container> {/*  Container      */}
        <BootstrapNavbar.Brand as={Link} to="/" style={{ fontWeight: 'bold', fontSize: '1.5rem' }}>
          TwentyThree
        </BootstrapNavbar.Brand>
        <BootstrapNavbar.Toggle aria-controls="responsive-navbar-nav" />
        <BootstrapNavbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            {/*    , :
            <Nav.Link as={Link} to="/features">Features</Nav.Link>
            <Nav.Link as={Link} to="/about">About Us</Nav.Link>
            */}
          </Nav>
          <Nav className="align-items-center"> {/*      */}
            {currentUser ? (
              //   
              <NavDropdown
                title={
                  <>
                    <Image
                      src={getFullImageUrl(currentUser.profilePictureUrl) || defaultAvatar}
                      alt={currentUser.nickname}
                      roundedCircle
                      width={30}
                      height={30}
                      className="me-2 border border-light" //    
                      onError={(e) => { e.target.onerror = null; e.target.src = defaultAvatar; }} //   ,  URL  
                    />
                    {/*          */}
                    <span className="d-none d-lg-inline">{currentUser.nickname}</span>
                  </>
                }
                id="user-nav-dropdown"
                align="end" //      
              >
                <NavDropdown.Item as={Link} to="/profile">
                  My Profile
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/chat">
                  Current Chat
                </NavDropdown.Item>
                {/*    ,  "Settings" */}
                <NavDropdown.Divider />
                <NavDropdown.Item onClick={handleLogout}>
                  Logout
                </NavDropdown.Item>
              </NavDropdown>
            ) : (
              //    
              <>
                <Nav.Link as={Link} to="/login">Login</Nav.Link>
                <Nav.Link as={Link} to="/register">Register</Nav.Link>
              </>
            )}
          </Nav>
        </BootstrapNavbar.Collapse>
      </Container>
    </BootstrapNavbar>
  );
};

export default Navbar;