import React, { createContext, useState, useEffect, useCallback } from 'react';
import { jwtDecode } from 'jwt-decode';
import apiClient from '../services/apiClient';
import { getUserProfile } from '../services/userService';

export const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  //    localStorage.    ,  null.
  const [token, setToken] = useState(() => localStorage.getItem('token'));
  const [loading, setLoading] = useState(true); //   

  const applyTokenToApiClient = (currentToken) => {
    if (currentToken) {
      apiClient.defaults.headers.common['Authorization'] = `Bearer ${currentToken}`;
    } else {
      delete apiClient.defaults.headers.common['Authorization'];
    }
  };

  const fetchCurrentUserDetails = useCallback(async (currentTokenForFetch) => {
    if (!currentTokenForFetch) {
      setCurrentUser(null);
      applyTokenToApiClient(null); // ,   
      setLoading(false);
      return null; //  null,   
    }

    applyTokenToApiClient(currentTokenForFetch); //    
    try {
      const userProfile = await getUserProfile();
      setCurrentUser(userProfile);
      setLoading(false);
      return userProfile; //   
    } catch (error) {
      console.error("AuthContext: Failed to fetch user details:", error);
      setCurrentUser(null);
      localStorage.removeItem('token'); //   
      setToken(null);                   //    
      applyTokenToApiClient(null);      //  
      setLoading(false);
      return null; //  null   
    }
  }, []); //   ,    (getUserProfile) 

  //       
  useEffect(() => {
    const initialToken = localStorage.getItem('token');
    if (initialToken) {
      try {
        const decoded = jwtDecode(initialToken);
        if (decoded.exp * 1000 > Date.now()) {
          setToken(initialToken); //    
          fetchCurrentUserDetails(initialToken); //  
        } else {
          console.log("AuthContext: Token expired on initial load.");
          localStorage.removeItem('token');
          setToken(null);
          setCurrentUser(null);
          applyTokenToApiClient(null);
          setLoading(false);
        }
      } catch (e) {
        console.error("AuthContext: Invalid token on initial load.", e);
        localStorage.removeItem('token');
        setToken(null);
        setCurrentUser(null);
        applyTokenToApiClient(null);
        setLoading(false);
      }
    } else {
      //  ,   
      applyTokenToApiClient(null); // ,   
      setCurrentUser(null);
      setLoading(false);
    }
  }, [fetchCurrentUserDetails]); //     


  const login = useCallback(async (newToken) => {
    if (!newToken) {
        console.error("AuthContext: Attempted to login with a null or undefined token.");
        //        
        return Promise.reject(new Error("Invalid token provided for login."));
    }
    localStorage.setItem('token', newToken);
    setToken(newToken); //    
    //   ,     
    //  ,  currentUser    .
    // setLoading(true)    , .. fetchCurrentUserDetails   loading.
    setLoading(true); // ,    ( +  )
    return fetchCurrentUserDetails(newToken); //  Promise  fetchCurrentUserDetails
  }, [fetchCurrentUserDetails]);

  const logout = useCallback(() => {
    localStorage.removeItem('token');
    setToken(null);
    setCurrentUser(null);
    applyTokenToApiClient(null); //    apiClient
    // setLoading(false)   , ..   
    //   logout    ,   , loading   
  }, []);

  const updateUserContext = useCallback((updatedUserData) => {
    setCurrentUser(prevUser => prevUser ? { ...prevUser, ...updatedUserData } : updatedUserData);
  }, []);

  const contextValue = {
    currentUser,
    token,
    loading,
    login,
    logout,
    updateUserContext,
    // fetchCurrentUserDetails   ,     
  };

  return (
    <AuthContext.Provider value={contextValue}>
      {children}
    </AuthContext.Provider>
  );
};