import apiClient from './apiClient'; //    Axios

/**
 *      .
 * @returns {Promise<object>} - Promise,    UserProfileDto.
 */
export const getUserProfile = async () => {
  try {
    const response = await apiClient.get('/users/me'); //     
    //  : UserProfileDto
    // { id, nickname, reputation, profilePictureUrl, createdAt, interests: [{id, name, description}] }
    return response.data;
  } catch (error) {
    console.error("Error fetching user profile:", error.response?.data || error.message);
    throw error;
  }
};

/**
 *     .
 * @param {object} profileData -     (UserUpdateDto).
 * @param {string} [profileData.nickname] -   ().
 * @param {number[]} [profileData.interestIds] -  ID   ().
 * @returns {Promise<object>} - Promise,     UserProfileDto.
 */
export const updateUserProfile = async (profileData) => {
  try {
    const response = await apiClient.put('/users/me', profileData); //     
    return response.data; //  :  UserProfileDto
  } catch (error) {
    console.error("Error updating user profile:", error.response?.data || error.message);
    throw error;
  }
};

/**
 *    .
 * @param {File} avatarFile -   .
 * @returns {Promise<object>} - Promise,     UserProfileDto (  URL ).
 */
export const updateUserAvatar = async (avatarFile) => {
  //     FormData
  const formData = new FormData();
  formData.append('file', avatarFile); // 'file' -   ,   

  try {
    const response = await apiClient.post('/users/me/avatar', formData, {
      headers: {
        // Axios   'Content-Type': 'multipart/form-data'
        //    FormData,     
        //       .
        //    .
        // 'Content-Type': 'multipart/form-data',
      },
    });
    return response.data; //  :  UserProfileDto
  } catch (error) {
    console.error("Error uploading user avatar:", error.response?.data || error.message);
    throw error;
  }
};