import axios from 'axios';

//   URL API   
//  .env     :
// REACT_APP_API_BASE_URL=http://localhost:8080/api
const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'https://brilliant-liberation-production.up.railway.app:8080/api';

//   axios   
const apiClient = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
    //     ,  
    // 'Accept': 'application/json',
  },
});

// Interceptor  :
//      ,   .
apiClient.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token'); // ,     localStorage   'token'
    if (token) {
      //   Authorization   Bearer
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config; //   
  },
  (error) => {
    //      (,  )
    console.error("Error in request interceptor:", error);
    return Promise.reject(error);
  }
);

// Interceptor   (,      ):
//     logout   401   .
apiClient.interceptors.response.use(
  (response) => {
    //   ,    2xx,    
    return response;
  },
  (error) => {
    //   ,     2xx,    
    if (error.response) {
      //   ,     ,     2xx
      console.error("API Error Response Status:", error.response.status);
      console.error("API Error Response Data:", error.response.data);

      if (error.response.status === 401) {
        //   (,    )
        //       .
        // ,    localStorage     .
        // :      AuthContext,   .
        // AuthContext        .
        //  AuthContext   (,     ),
        //  interceptor     .
        console.warn("Received 401 Unauthorized from API. User session might be invalid.");
        // localStorage.removeItem('token'); //   AuthContext.logout()
        // if (!window.location.pathname.includes('/login')) { //   
        //   window.location.href = '/login'; //  ,   useNavigate  AuthContext
        // }
      } else if (error.response.status === 403) {
        //   ( ,       )
        console.warn("Received 403 Forbidden from API.");
      }
      //         
    } else if (error.request) {
      //   ,     
      // `error.request`   XMLHttpRequest    
      // http.ClientRequest  node.js
      console.error("API Error: No response received for the request.", error.request);
      //         
      //       
      return Promise.reject(new Error("Network error or server is not responding. Please try again later."));
    } else {
      //  -   ,   
      console.error("API Error: Error setting up the request.", error.message);
    }
    return Promise.reject(error); //       
  }
);

export default apiClient;