import React from 'react';
import { Navigate, Outlet, useLocation } from 'react-router-dom';
import { useAuth } from '../../hooks/useAuth'; //      AuthContext
import { FullPageSpinner } from '../common/LoadingSpinner'; //    

const ProtectedRoute = () => {
  const { currentUser, loading: authLoading } = useAuth(); //       AuthContext
  const location = useLocation(); //    URL     

  //     ,  
  //  ,      
  if (authLoading) {
    return <FullPageSpinner message="Verifying your session..." />;
  }

  //     (currentUser is null),
  //    .
  // `state={{ from: location }}`     location state  /login,
  //          .
  // `replace`      ,   ""
  //      ,      .
  if (!currentUser) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  //   ,    .
  // <Outlet />     react-router-dom v6,  
  //   UI,  URL      .
  return <Outlet />;
};

export default ProtectedRoute;